<template>
  <background-transparent-view :closeModal="closeModal" :backgroundModal="backgroundModal"></background-transparent-view>
  <div v-if="isLoading" class="d-flex align-items-center" style="height: 100%">
    <div class="loading-spinner"></div>
  </div>
  <div v-else class="container-fluid">
    <header-view :catologModalFunc="catologModalFunc" :catologModal="catologModal" :loginModal="loginModal" :favourite_products="getFavourites.length" :loginModalFunc="loginModalFunc"></header-view>
    <div class="carusel_modal" v-if="createBankCard"  @click="closeModal">
      <div class="d-flex align-items-center">
        <div class="bank_card_modal_content" @click.stop>
          <div id="custCarousel" class="carousel slide">
            <div class="carousel-inner text-start">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="bank_card_name">{{$t('Enter card name')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="text" class="form-control" id="bank_card_name" v-model="card_name" placeholder="Kapitalbank">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="bank_card_user_name">{{$t('Enter card user name')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="text" class="form-control" id="bank_card_user_name" v-model="card_user_name" placeholder="Surname username">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="bank_card_validity_period">{{$t('Enter validity period')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="date" class="form-control" id="bank_card_validity_period" v-model="card_validity_period" placeholder="06-06-2026">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="bank_card">{{$t('Enter your bank card')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="number" class="form-control" id="bank_card" v-model="card_number" placeholder="8600 0000 0000 0000">
              <div data-aos="zoom-in" data-aos-duration="444" class="d-flex justify-content-between mt-2">
                <button class="btn btn-danger" @click="closeModal">{{$t('Cancell')}}</button>
                <button class="submit-button" @click="saveBankCard">{{$t('Create')}}</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="carusel_modal" v-if="editBankCard"  @click="closeModal">
      <div class="d-flex align-items-center" v-if="getEditCard">
        <div class="bank_card_modal_content" @click.stop>
          <div id="custCarousel" class="carousel slide">
            <div class="carousel-inner text-start">
              <input data-aos="zoom-in" data-aos-duration="444" type="hidden" id="card_id" :value="getEditCard.id">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_name">{{$t('Enter card name')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="text" class="form-control" id="edit_bank_card_name" :value="getEditCard.name" placeholder="Kapitalbank">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_user_name">{{$t('Enter card user name')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="text" class="form-control" id="edit_bank_card_user_name" :value="getEditCard.user_name" placeholder="Surname username">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_validity_period">{{$t('Enter validity period')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" v-if="getEditCard.validity_period" type="date" class="form-control" id="edit_bank_card_validity_period" :value="getEditCard.validity_period.split(' ')[0]" placeholder="06-06-2026">
              <input data-aos="zoom-in" data-aos-duration="444" v-else type="date" class="form-control" id="edit_bank_card_validity_period" placeholder="06-06-2026">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_number">{{$t('Enter your bank card')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="number" class="form-control" id="edit_bank_card_number" :value="getEditCard.card_number" placeholder="8600 0000 0000 0000">
              <div data-aos="zoom-in" data-aos-duration="444" class="d-flex justify-content-between mt-2">
                <button class="btn btn-danger" @click="closeModal">{{$t('Cancell')}}</button>
                <button class="submit-button" @click="updateBankCard">{{$t('Update')}}</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="d-flex align-items-center" v-else>
        <div class="bank_card_modal_content" @click.stop>
          <div id="custCarousel" class="carousel slide">
            <div class="carousel-inner text-start">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_name">{{$t('Enter card name')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="text" class="form-control" id="edit_bank_card_name" placeholder="Kapitalbank">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_user_name">{{$t('Enter card user name')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="text" class="form-control" id="edit_bank_card_user_name" placeholder="Surname username">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_validity_period">{{$t('Enter validity period')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="date" class="form-control" id="edit_bank_card_validity_period" placeholder="06-06-2026">
              <label data-aos="zoom-in" data-aos-duration="444" class="mb-2" for="edit_bank_card_number">{{$t('Enter your bank card')}}</label>
              <input data-aos="zoom-in" data-aos-duration="444" type="number" class="form-control" id="edit_bank_card_number" placeholder="8600 0000 0000 0000">
              <div data-aos="zoom-in" data-aos-duration="444" class="d-flex justify-content-between mt-2">
                <button class="btn btn-danger" @click="closeModal">{{$t('Cancell')}}</button>
                <button class="submit-button" @click="updateBankCard">{{$t('Update')}}</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="carusel_modal" v-if="deleteBankCard" @click="closeModal">
      <div class="d-flex align-items-start" v-if="getDeleteCard">
        <div class="map_content" @click.stop>
          <div id="custCarousel" class="carousel slide">
            <div class="carousel-inner">
              <div>
                <span data-aos="zoom-in" data-aos-duration="444">{{ getDeleteCard.name }}</span>&nbsp;
                <span data-aos="zoom-in" data-aos-duration="444">{{ getDeleteCard.user_name }} </span>&nbsp;
                <span data-aos="zoom-in" data-aos-duration="444">{{ getDeleteCard.card_number }} </span>
              </div>
              <div>
                <span data-aos="zoom-in" data-aos-duration="444">{{$t('Are you sure you want to delete this card ?')}}</span>
              </div>
              <div class="d-flex justify-content-around mt-2">
                <button data-aos="zoom-in" data-aos-duration="444" class="btn btn-warning" @click="closeModal">{{$t('Cancell')}}</button>
                <button data-aos="zoom-in" data-aos-duration="444" class="btn btn-danger" @click="deleteCard(getDeleteCard.id)">{{$t('Delete')}}</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <section class="mt-5">
      <div class="container d-flex">
        <ul class="d-flex" style="margin-left: -35px;">
          <li class="d-flex ms-1">
            <router-link data-aos="zoom-in" data-aos-duration="444" to="/" style="text-decoration: none; color: #000000;">
              {{$t("Home")}}
            </router-link>
            <div class="ms-1">
              <img data-aos="zoom-in" data-aos-duration="444" src="../assets/uploads/icons/arrow-left.svg" alt="arrow-left">
            </div>
          </li>
          <li class="d-flex ms-1">
            <router-link data-aos="zoom-in" data-aos-duration="444" to="/" style="text-decoration: none; color: #000000;">
              {{$t("your personal account")}}
            </router-link>
          </li>
        </ul>
      </div>
    </section>
    <section>
      <div class="container d-flex profile_data-adaptive">
        <personal-info-menus-view :current_menu="current_menu"></personal-info-menus-view>
        <div class="personal_info_content">
          <div class="d-flex flex-column">
            <personal-information-view></personal-information-view>
            <bank-product-card-view :getCards="getCards"></bank-product-card-view>
          </div>
        </div>
      </div>
    </section>
    <section>
      <div class="container detail_container mt-5" style="height: 464px">
        <h3 class="comment_title pt-4" data-aos="zoom-in" data-aos-duration="444">{{$t("You may like")}}</h3>
        <swiper-product></swiper-product>
      </div>
    </section>
    <footer-view></footer-view>
  </div>
</template>
<script>
import {mapActions, mapGetters, mapMutations, mapState} from "vuex"
import axios from "axios";

export default {
  computed:{
    ...mapState(["backgroundModal", "catologModal", "loginModal", "createBankCard", "editBankCard", "deleteBankCard"]),
    ...mapGetters(["getCards", "getEditCard", "getDeleteCard", "getFavourites"])
  },
  data(){
    return{
      name:"Elyor",
      current_menu: {page:'profile_pay'},
      isLoading:true,
      card_name:'',
      card_user_name:'',
      card_validity_period:'',
      card_number:'',
      card_id:'',
      edit_card_name:'',
      edit_card_user_name:'',
      edit_card_validity_period:'',
      edit_card_number:'',
    }
  },
  methods:{
    ...mapMutations(["catologModalFunc", "closeModal", "loginModalFunc", "SET_CARDS", "getFavouritedProducts"]),
    ...mapActions(["GETCARDS"]),
    async saveBankCard(){
      try{
        const response = await axios.post(process.env.VUE_APP_SERVER+'/store-card', {
          card_name:this.card_name,
          card_user_name:this.card_user_name,
          card_validity_period:this.card_validity_period,
          card_number:this.card_number,
        },
        {
          headers:{
            'Access-Control-Allow-Origin': '*',
            'Content-Type':'application/x-www-form-urlencoded',
            "Authorization":`Bearer ${localStorage.getItem('user_token')}`
          }
        })
        if(response.data.status == true){
          this.$toast.success(this.$t('Card added successfully !'));
        }
        location.reload();
      }catch (e) {
        console.log(e)
      }
    },
    async updateBankCard(){
      try{
        this.edit_card_name = document.getElementById("edit_bank_card_name").value
        this.edit_card_user_name = document.getElementById("edit_bank_card_user_name").value
        this.edit_card_validity_period = document.getElementById("edit_bank_card_validity_period").value
        this.edit_card_number = document.getElementById("edit_bank_card_number").value
        this.card_id = document.getElementById("card_id").value
        const response = await axios.post(process.env.VUE_APP_SERVER+'/update-card', {
          id:this.card_id,
          card_name:this.edit_card_name,
          card_user_name:this.edit_card_user_name,
          card_validity_period:this.edit_card_validity_period,
          card_number:this.edit_card_number,
        },
        {
          headers:{
            'Access-Control-Allow-Origin': '*',
            'Content-Type':'application/x-www-form-urlencoded',
            "Authorization":`Bearer ${localStorage.getItem('user_token')}`
          }
        })
        if(response.data.status == true){
          this.$toast.success(this.$t('Card updated successfully !'));
        }
        location.reload();
      }catch (e) {
        console.log(e)
      }
    },
    async deleteCard(card_id){
      try{
        const response = await axios.post(process.env.VUE_APP_SERVER+'/destroy-card', {
          id:card_id,
        },
        {
          headers:{
            'Access-Control-Allow-Origin': '*',
            'Content-Type':'application/x-www-form-urlencoded',
            "Authorization":`Bearer ${localStorage.getItem('user_token')}`
          }
        })
        if(response.data.status == true){
          if(response.data.message == 'prohibited'){
            this.$toast.warning(this.$t('You cannot delete this address because here is order associated with this address.'))
          }else {
            this.$toast.success(this.$t('Card deleted successfully !'));
            location.reload();
          }
        }
      }catch (e) {
        console.log(e)
      }
    },
  },
  mounted() {
    this.getFavouritedProducts()
    this.GETCARDS()
    this.SET_CARDS()
    setTimeout(() => {
      this.isLoading = false; // Set to false when the loading is complete
    }, 444);
  }
}
</script>
<style>
.bank_card_modal_content{
  z-index: 8;
  background-color: white;
  border-radius: 4px;
}
</style>